import React, {useState, useEffect} from 'react';
// import logo from './logo.svg';
import './App.css';

const StarDisplay = props =>(
  <>
  {utils.range(1, props.count).map(starId =>
    <div key={starId} className='star' />
    )}
  </>
);

const PlayNumber = props => (
  <button  className='number'
  style={{backgroundColor: colors[props.status]}}
  onClick={() => props.onClick(props.number, props.status)}
  >
    {props.number}
    </button>
);

const PlayAgain = props =>(
  <div className='game-done'>
    <div className='message'
    style={{color: props.gameStatus === 'Lost'? 'red': 'green'}}>
      {props.gameStatus === 'Lost'? 'Game Over': 'Nice'}
    </div>
    <button onClick={props.onClick}>
    Play Again
  </button>
  </div>
  
)

const useGameState = () => {
  const [stars, setStars] = useState(utils.random(1,9));
  const [availableNum, setAvailableNums] = useState(utils.range(1,9));
  const [candidateNum, setCandidateNums] = useState([]);
  const [secondsLeft, setSecondLeft] = useState(10);

  useEffect(() =>{
    if (secondsLeft > 0) {
      const timerId = setTimeout(() =>{
      setSecondLeft(secondsLeft - 1);
    }, 1000);
    return () => clearTimeout(timerId)
  }});

  const setGameState= (newCandidateNums) =>{
    if (utils.sum(newCandidateNums) !== stars){
      setCandidateNums(newCandidateNums);
    }
    else {
      const newAvailableNums = availableNum.filter(
        n => !newCandidateNums.includes(n)
      );
      setStars(utils.randomSumIn(newAvailableNums, 9))
      setAvailableNums(newAvailableNums);
      setCandidateNums([]);
    }
  };

  return {stars, availableNum, candidateNum, secondsLeft, setGameState}
};

const Game = (props) =>{
  const {
    stars,
    availableNum,
    candidateNum,
    secondsLeft,
    setGameState
  } = useGameState();

  const WrongCandidate = utils.sum(candidateNum) > stars;
  const gameStatus = availableNum.length === 0 
    ? 'won'
    : secondsLeft === 0 ? 'Lost' : 'active'

  const numberStatus = (number) =>{
    if (!availableNum.includes(number)){
      return 'used';
    }
    if (candidateNum.includes(number)){
      return WrongCandidate? 'wrong': 'candidate';
    }
    return 'available';
  }

  const onNumberClick = (number, currentStatus) =>{
    if (currentStatus == 'used') {
      return;
    }

    const newCandidateNums = 
       currentStatus === 'available'
        ? candidateNum.concat(number)
        : candidateNum.filter(cn => cn !== number);
      
    setGameState(newCandidateNums)
  }

  return (
    <div className='game'>
      <div className="help">
        Pick 1 or more numbers that sum to the number of the stars 
      </div>

      <div className="body">
        <div className='left'>
          {
            gameStatus !== 'active'? (
              <PlayAgain onClick={props.startNewGame} gameStatus={gameStatus} />
            ):
            (
              <StarDisplay count = {stars}/>
            )
          }
  
        </div>
        <div className='right'>
          {utils.range(1, 9).map(number =>
          <PlayNumber 
          key={number} 
          status = {numberStatus(number)}
          number = {number}
          onClick = {onNumberClick}
           />
          )}
        </div>
    </div>
    <div>
      <div className='timer'>Time Remaining: {secondsLeft}</div>
    </div>
  </div>
  )
};

const StarMatch = () =>{
  const [gameId, setGameId] = useState(1)
  return <Game key={gameId} startNewGame ={()=> setGameId(gameId + 1)} />
}

{/* Color Theme */}
const colors = {
  available: 'lightgray',
  used: 'lightgreen',
  wrong: 'lightcoral',
  candidate: 'deepskyblue',
};

{/* Math science */}
const utils = {
  // Sum an array
  sum: arr => arr.reduce((acc, curr) => acc + curr, 0),

  // create an array of numbers between min and max (edges included)
  range: (min, max) => Array.from({ length: max - min + 1 }, (_, i) => min + i),

  // pick a random number between min and max (edges included)
  random: (min, max) => min + Math.floor(Math.random() * (max - min + 1)),

  // Given an array of numbers and a max...
  // Pick a random sum (< max) from the set of all available sums in arr
  randomSumIn: (arr, max) => {
    const sets = [[]];
    const sums = [];
    for (let i = 0; i < arr.length; i++) {
      for (let j = 0, len = sets.length; j < len; j++) {
        const candidateSet = sets[j].concat(arr[i]);
        const candidateSum = utils.sum(candidateSet);
        if (candidateSum <= max) {
          sets.push(candidateSet);
          sums.push(candidateSum);
        }
      }
    }
    return sums[utils.random(0, sums.length - 1)];
  },
};

function App() {
return (
  <div>
    <StarMatch />
  </div>
  )
};

export default App;
